@charset "UTF-8";
@import url(~malihu-custom-scrollbar-plugin/jquery.mCustomScrollbar.css);
@import url(~angular-loading-bar/src/loading-bar.css);
@import url(~angular-material/modules/js/sidenav/sidenav.css);
main-content .mCSB_inside > .mCSB_container {
  margin-right: 0 !important;
  margin-left: 0 !important;
  min-height: 200px; }

main-content .mCSB_inside .mCSB_scrollTools {
  left: -7px !important;
  right: auto !important; }

main-content [custom-scroll-bar] loading {
  align-items: flex-end; }

main-content .table-container {
  min-height: 300px;
  height: 500px; }

main-content div.header {
  height: 40px;
  border-bottom: 1px solid #d0d0d0; }
  main-content div.header > span {
    padding: 10px 5px; }

main-content div.title {
  height: 40px;
  border-bottom: 1px solid #d0d0d0; }
  main-content div.title span i.zmdi-plus {
    font-size: 22px;
    padding-left: 5px; }
  main-content div.title span i.zmdi-delete, main-content div.title span i.zmdi-edit {
    font-size: 20px;
    padding-right: 8px; }

main-content tr.selected {
  background-color: #f2f2f2; }
  main-content tr.selected td:last-child {
    position: relative; }
    main-content tr.selected td:last-child:after {
      content: '\f2fA';
      font: normal normal normal 28px 'Material-Design-Iconic-Font';
      position: absolute;
      left: 11px;
      vertical-align: middle;
      top: calc(50% - 16px);
      /* bottom: 0; */ }

placement-test .mCSB_inside > .mCSB_container {
  margin-right: 0 !important;
  margin-left: 0 !important;
  min-height: 200px; }

placement-test .mCSB_inside .mCSB_scrollTools {
  left: -7px !important;
  right: auto !important; }

placement-test [custom-scroll-bar] loading {
  align-items: flex-end; }

placement-test .table-container {
  min-height: 300px;
  height: 500px; }

placement-test div.header {
  height: 40px;
  border-bottom: 1px solid #d0d0d0; }
  placement-test div.header > span {
    padding: 10px 5px; }

placement-test div.title {
  height: 40px;
  border-bottom: 1px solid #d0d0d0; }
  placement-test div.title span i.zmdi-plus {
    font-size: 22px;
    padding-left: 5px; }
  placement-test div.title span i.zmdi-delete,
  placement-test div.title span i.zmdi-edit {
    font-size: 20px;
    padding-right: 8px; }

placement-test tr.selected {
  background-color: #f2f2f2; }
  placement-test tr.selected td:last-child {
    position: relative; }
    placement-test tr.selected td:last-child:after {
      content: '\f2fA';
      font: normal normal normal 28px 'Material-Design-Iconic-Font';
      position: absolute;
      left: 11px;
      vertical-align: middle;
      top: calc(50% - 16px);
      /* bottom: 0; */ }

placement-test .remove-row {
