input-container {
  position: relative;
  display: block; }
  input-container input {
    margin-bottom: 0 !important; }
    input-container input:focus {
      box-shadow: inherit !important;
      border-bottom: 1px solid #9e9e9e !important; }
    input-container input.ng-not-empty ~ label {
      font-size: .8rem;
      -webkit-transform: translateY(-140%);
      transform: translateY(-140%);
      color: #03a9f4; }
  input-container:after {
    position: absolute;
    right: 0;
    z-index: 3;
    bottom: 0;
    height: 2px;
    width: 0;
    content: "";
    transition: all;
    transition-duration: 300ms;
    background: #03a9f4; }
  input-container.active:after, input-container.has-value:after {
    width: 100%; }
  input-container.active label:not(.label-icon), input-container.has-value label:not(.label-icon) {
    font-size: .8rem;
    -webkit-transform: translateY(-140%);
    transform: translateY(-140%);
    color: #03a9f4; }
  input-container label {
    left: auto;
    right: 0; }
  input-container.rtl {
    direction: rtl; }
    input-container.rtl label {
      left: auto;
      right: 0; }
  input-container.ltr {
    direction: ltr; }
    input-container.ltr label {
      right: auto;
      left: 0; }
  input-container.has-icon {
    margin-right: 3rem; }
    input-container.has-icon i,
    input-container.has-icon .icon {
      position: absolute;
      right: -2.5rem;
      font-size: 2rem;
      top: 0.5rem; }

