placement-test {
  .mCSB_inside {
    >.mCSB_container {
      margin-right: 0 !important;
      margin-left: 0 !important;
      min-height: 200px;
    }
    .mCSB_scrollTools {
      left: -7px !important;
      right: auto !important;
    }
  }
  [custom-scroll-bar] {
    loading {
      align-items: flex-end;
    }
  }
  .table-container {
    min-height: 300px;
    height: 500px;
  }
  div.header {
    height: 40px;
    border-bottom: 1px solid #d0d0d0;
    >span {
      padding: 10px 5px;
    }
  }
  div.title {
    height: 40px;
    border-bottom: 1px solid #d0d0d0;
    span {
      i.zmdi-plus {
        font-size: 22px;
        padding-left: 5px;
      }
      i.zmdi-delete,
      i.zmdi-edit {
        font-size: 20px;
        padding-right: 8px;
      }
    }
  }
  tr.selected {
    background-color: #f2f2f2;
    td:last-child {
      position: relative;
      &:after {
        content: '\f2fA';
        font: normal normal normal 28px 'Material-Design-Iconic-Font';
        position: absolute;
        left: 11px;
        vertical-align: middle;
        top: calc(50% - 16px);
        /* bottom: 0; */
      }
    }
  }
  .remove-row {
    height: 70px;
    a {
      font-size: 25px;
      margin-top: 14px;
    }
  }
}


